body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ссылки */
.navbar-brand {
    padding: 0;
    margin: 0;
}


.container {
    padding-top: 40px;
    padding-bottom: 40px;
}

.App {

    /* padding: 50px 0; */
    min-height: 100vh;
}

.border {
    border: 1px rgba(0,0,0,.125) solid;
}

.rate_drop_down {
    display: block;
    opacity: 0;
    width: 280px;
    height: 60px;
    position: absolute;
    top: 80px;
    left: -1000px;
    z-index: 51;
    padding: 10px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    background-color: #fff;
    transition: all 0.2s ease-out;
    pointer-events: none;
}

.rate_drop_down.visible {
    opacity: 1;
    top: 80px;
    left: 0;
    pointer-events: auto;
}
.main_header {
    /* background-color: rgb(95, 143, 175); */
    padding: 5px;
}

.login {
    text-align: right;
}

.logo {
    width: 45px;
    height: 45px;
}
.search_form {
    width: 100%;
    display: inline-block;
}

.search_form label {
    width: 50%;
    min-width: 380px;
    display: inline-block;
}
.search_form input {
    width: 80%;
}

.dropdown_search_container {
    position: relative;
    width: 80%;
    /* height: 1px; */
}


.dropdown_search {
    display: none;
}

.dropdown_search.visible {
    display: block;
    z-index: 100;
    position: absolute;
    width: 100%;
    
    border: 1px solid rgba(0, 0, 0, 0.125);
    top: 0;
    left: 0;
    background-color: #fff;
    color: #000;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
}


.dropdown_search div { 
    padding: 5px 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
    color: grey;
}

.dropdown_search img {
    height: 30px;
    margin-right: 10px;
}



.dropdown-menu a {

    display: block;
    padding: 0 15px;
}

/* ---------------------------- left_side ------------------------------ */




/* .tittle {
    text-align: center;
} */


.sides_wrapper {
    width: 100%;
    display: flex;
    justify-self: flex-start;
    align-items: flex-start;
}

/* .left_side, .right_side {
    padding: 10px;
} */

.left_side {
    width: 30%;
    min-width: 280px;
    max-width: 280px;
    margin-right: 15px;
}


.left_side div {
    padding: 20px;
    margin-bottom: 15px;
   
}

.left_side span {
    display: inline-block;
    padding: 2px 10px;
    margin: 5px;
    border-radius: 7px;
    /* white-space: pre-line; */
}
.left_side span.active {
    background-color: rgb(103, 130, 252);
}

.left_side span:hover {
    background-color: rgb(147, 167, 253);
    cursor: pointer;
}

.request_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 50px;
    background-color: rgb(1, 114, 1);
    color: #fff;
    z-index: 100;
    cursor: pointer;
}




.right_side {
    width: 70%;
    padding: 1rem;
}



/* ---------------------------- right_side ------------------------------ */

.popular_movies {

    width: 100%;
 }



.popular_movies h4 {
   text-align: center;
   margin-bottom: 30px;
   text-transform: uppercase;
}



.card_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    /* padding-top: 10px; */
    padding-bottom: 10px;
}


.card_wrapp {
    position: relative;
    margin-bottom: 10px;
    /* border: 1px rgba(0,0,0,.125) solid; */
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}

.card_wrapp .rate_btn {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 50;
}

.rate_btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 33px;
    width: 33px;
    color: gray;
    font-size: 26px;
    /* box-shadow: -4px 4px 5px rgba(255, 255, 255, 0.3); */
    /* padding: 7px; */
    /* border-radius: 50%; */
    /* border: 1px #000 solid; */
    background-color: rgb(255, 255, 255, 1);
}

.rate_btn:hover {
    color: red;
}

.card {
    /* padding: 10px; */
    /* margin: 3px; */
    min-width: 170px;
    min-height: 245px;
    cursor: pointer;
    /* box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5); */
}

.content {
    
    overflow: hidden;
    position: relative;
    
}

.content img {
    /* margin: 10px; */
    width: 168px;
    height: 243px;
    transition: 0.5s all ease;
}


.content:hover>.description {
    opacity: 1;
    bottom: 0;
}

.content:hover>img {
    -webkit-filter: blur(1px);
            filter: blur(1px);
    /* filter: grayscale(80%); */
}

.description {
    padding: 15px 8px 15px 8px;
    position: absolute;
    width: 100%;
    bottom: -100px;
    left: 0;
    /* background-color: rgba(0, 0, 0, 0.7);
    color: #fff; */
    background-color: rgba(255, 255, 255, 1);
    background-color: rgba(0, 0, 0, 0.9);
    color: #000;
    color: #fff;

    transition: 0.7s all ease;
    opacity: 0;
    pointer-events: none;
    text-align: center;
}


.description span {
    /* line-height: 0.8em; */
    display: block;
    width: 100%;
    /* word-wrap: break-word; */
    white-space: pre-line;
    font-size: 0.7em;
}


.rate_stars_container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
    display: flex;
    background-color: rgb(255, 255, 255);
}

.rate_star_wrap {
    display: flex;
    /* justify-content: flex-end; */
    align-items: center;
    width: 100%;
}



.rate_star {
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 20px;
    width: 20px;
    padding: 2px;
    /* float: left; */
    background-color: rgb(88, 88, 88); 
    
    -webkit-clip-path: polygon(
        50% 0%,
        63% 38%,
        100% 38%,
        69% 59%,
        82% 100%,
        50% 75%,
        18% 100%,
        31% 59%,
        0% 38%,
        37% 38%
    ); 
    
            clip-path: polygon(
        50% 0%,
        63% 38%,
        100% 38%,
        69% 59%,
        82% 100%,
        50% 75%,
        18% 100%,
        31% 59%,
        0% 38%,
        37% 38%
    );
}


.rate_star.active {
    background-color: rgb(234, 255, 47);
}

.rate_star.red {
    background-color: red;
}

.remove_rate {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    color: red;
    /* background-color: red; */
    margin: 0 10px;
}

.remove_rate:hover {
   
    color: #fff;
    background-color: red;
   
}

.pagination_container {
    display: flex;
    justify-content: center;
    width: 100%;
    /* height: 50px; */
    /* text-align: center; */
}

.pagination {
    margin-top: 5px;
    margin-bottom: 5px;
}
.footer {
    width: 100%;
    height: 100px;
    background-color: rgb(5, 44, 28);
    color: #fff;
}
